/*----------- images -----------*/

$sprite:							url( ../../media/img/sprite.png ) !default;
$sprite_x2:							url( ../../media/img/sprite_x2.png ) !default;
$sprite-size:						100px 100px;

/*----------- images (end) -----------*/





/*----------- fonts -----------*/

$font-path:							"../fonts" !default;

/*----------- fonts (end) -----------*/





/*----------- usage -----------*/

$_use_buttons:						true;
$_use_common:						true;
$_use_forms:						true;
$_use_root:							true;
$_use_tables:						true;
$_use_typography:					true;

$_use_container:					true;
$_use_float:						true;
$_use_grid:							true;
$_use_modifiers:					true;

/*----------- usage (end) -----------*/





/*----------- typography -----------*/

$font-family:						'Poppins', sans-serif;
$font-family-base:					$font-family;

$font-size-xxxl:					3.6rem;
$font-size-xxl:						3rem;
$font-size-xl:						2.4rem;
$font-size-lg:						2rem;
$font-size-xm:						1.8rem;
$font-size-md:						1.6rem;
$font-size-sm:						1.4rem;
$font-size-xs:						1.3rem;
$font-size-xxs:						1.2rem;
$font-size-xxxs:					1rem;
$font-size-base:					$font-size-md;

$font-size-h1:						3.6rem;
$font-size-h2:						3rem;
$font-size-h3:						2.4rem;
$font-size-h4:						1.8rem;
$font-size-h5:						$font-size-base;
$font-size-h6:						1.2rem;

$font-size-body:					$font-size-base;
$font-size-main:					10px;

$l_s:								.06em;

$mb-h1:								20px;
$mb-h2:								15px;
$mb-h3:								10px;
$mb-h4:								7px;
$mb-h5:								5px;
$mb-h6:								5px;

$line-height-base:					1.35;
$line-height-medium:				1.5;
$line-height-computed:				floor( $font-size-base * $line-height-base ); // ~20px

$headings-font-family:				inherit;
$font-weight-base:					400;
$font-weight-medium:				500;
$headings-font-weight:				$font-weight-base;
$headings-line-height:				$line-height-base;
$headings-color:					inherit;

/*----------- typography (end) -----------*/





/*----------- common -----------*/

$n-col:								12;
$p_horiz-col:						15px;
$p_horiz-col_tb:					10px;
$v_align-col:						top;

$w-min:								320px;
$h-min:								100vh;
$img:								'../../media/img' !default;

$m-col:								$p_horiz-col * 2;
$m-col_tb:							$p_horiz-col_tb * 2;

$brd_w:								1px;

$w-container:						1170px;
$w-container_tb:					100%;
$w-container_full_w:				100%;

$anim_dur-b-modal:					.2s;

$anim_dur-b-list-examples:			.5s;



$z_i-b-header:						99;
$z_i-b-state:						$z_i-b-header + 1;

/*----------- common (end) -----------*/





/*----------- media -----------*/

$md-desk_md:						1440px;
$md-desk_sm:						1250px;
$md-tablet:							$w-container;
$md-mobile:							640px;
$md-mobile_sm:						320px;

/*----------- media (end) -----------*/





/*----------- link -----------*/

$brd_w-link:						1px;

/*----------- link (end) -----------*/





/*----------- form -----------*/

$m_b-label:							20px;
$f_sz-label:						$font-size-base;
$f_w-label:							700;

$h-input:							34px;
$h-textarea:						74px;

$brd_w-input:						1px;
$brd_rd-input:						0;

$p_vert-input:						6px;
$p_horiz-input:						12px - $brd_w-input;

$m-row-input:						10px;
$m-row-input-mb:					$m-row-input;
$m-form-body:						$m-row-input*2;

$f_sz-input:						$font-size-body;
$l_s-input:							.06em;

$per-input_col-many:				50%;

$w-checkbox:						14px;
$h-checkbox:						$w-checkbox;
$brd_w-checkbox:					1px;
$brd_w-checkbox_checked:			5px;
$brd_rd-checkbox:					2px;

$w-radio:							14px;
$h-radio:							$w-radio;
$brd_w-radio:						1px;
$brd_w-radio_checked:				5px;
$brd_rd-radio:						50%;

/*----------- form (end) -----------*/






/*----------- table -----------*/

$p_vert-td:							10px;
$p_horiz-td:						10px;

/*----------- table (end) -----------*/






/*----------- button -----------*/

$w-btn:								auto;
$w-btn_lg:							auto;
$w-btn_sm:							auto;
$w-btn_xs:							auto;



$h-btn:								auto;
$h-btn_wanted:						54px;
$h-btn_lg:							auto;
$h-btn_lg_wanted:					50px;
$h-btn_sm:							auto;
$h-btn_sm_wanted:					36px;
$h-btn_xs:							auto;
$h-btn_xs_wanted:					32px;



$brd_w-btn:							1px;
$brd_w-btn_lg:						2px;
$brd_w-btn_sm:						1px;
$brd_w-btn_xs:						1px;



$brd_rd-btn:						0;
$brd_rd-btn_lg:						0;
$brd_rd-btn_sm:						0;
$brd_rd-btn_xs:						0;



$f_sz-btn:							13px;
$f_sz-btn_lg:						$font-size-lg;
$f_sz-btn_sm:						$font-size-sm;
$f_sz-btn_xs:						$font-size-sm;



$f_w-btn:							700;
$l_s-btn:							.06em;



$lh-btn:							round( $f_sz-btn * 1.5 );
$lh-btn_lg:							round( $f_sz-btn_lg/1rem * 10 * 1.5 ) * 1px; // $f_sz-btn in rem
$lh-btn_sm:							round( $f_sz-btn_sm/1rem * 10 * 1.5 ) * 1px; // $f_sz-btn in rem
$lh-btn_xs:							round( $f_sz-btn_xs/1rem * 10 * 1.5 ) * 1px; // $f_sz-btn in rem



$p_vert-btn:						( $h-btn_wanted - $lh-btn - $brd_w-btn * 2 )/2;
$p_horiz-btn:						15px;

$p_vert-btn_lg:						( $h-btn_lg_wanted - $lh-btn_lg - $brd_w-btn_lg * 2 )/2;
$p_horiz-btn_lg:					30px;

$p_vert-btn_sm:						( $h-btn_sm_wanted - $lh-btn_sm - $brd_w-btn_sm * 2 )/2;
$p_horiz-btn_sm:					20px;

$p_vert-btn_xs:						( $h-btn_xs_wanted - $lh-btn_xs - $brd_w-btn_xs * 2 )/2;
$p_horiz-btn_xs:					15px;



$per-btn_hover:						10%;
$per-btn_active:					-10%;

/*----------- button (end) -----------*/
